.app-wrapper {
    width: auto;
    margin: 0 auto;
    display: grid;

    grid-template-areas:
        "h h"
        "n c";
    grid-template-rows: 140px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;

}
.app-wrapper-content {
    grid-area: c;
    background-color: cornflowerblue;

}